import React from 'react';
import logo from './logo.svg';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = { currentTemp: "",
                   tomorrowTemp: "",
                   location: "Helsinki" }
    
    this.handleChange = this.handleChange.bind(this);
    this.fetchWeatherData = this.fetchWeatherData.bind(this);
  }

  componentDidMount() {
    this.fetchWeatherData(this.state.location);
  }

  fetchWeatherData(location) {
    var url = "http://opendata.fmi.fi/wfs?service=WFS&version=2.0.0&request=getFeature&storedquery_id=fmi::forecast::hirlam::surface::point::multipointcoverage&place=" + location;
    fetch(url)
    .then((response) => {
      if (response.status !== 200) {
        console.log("Problem: " + response.status);
      } else {
        return response.text();
      }
    })
    .then((text) => {
      var parser = new DOMParser();
      var responseParsed = parser.parseFromString(text, "application/xml");
      window.responseParsed = responseParsed;
      var perHour = responseParsed.querySelector("doubleOrNilReasonTupleList").innerHTML.split("\n");
      var perHourTrimmed = perHour.slice(1, perHour.length-1).map((value) => value.trim());
      var weatherData = perHourTrimmed.map((value) => value.split(" "));
      this.setState({
        currentTemp: weatherData[0][1],
        tomorrowTemp: weatherData[23][1]
      });
    })
    .catch((err) => {
      console.log("Error: ", err);
    })
  }

  handleChange(event) {
    if (event.key === "Enter") {
      this.setState({location: event.target.value});
      this.fetchWeatherData(event.target.value);
    }
  }
  
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <input type="text" 
                 className="LocationInput" 
                 defaultValue={this.state.location} 
                 onKeyPress={this.handleChange} />
          <h2>Current location: { this.state.location } </h2>
        </header>
        <body className="App-body">
          <div className="Temperature">
            <h4>Current Temperature</h4>
            { this.state.currentTemp }
          </div>
          <div className="Temperature">
            <h4>Tomorrow's Temperature</h4>
            { this.state.tomorrowTemp }
          </div>
        </body>
      </div>
    );
  }
  
}

export default App;
